@import url(https://fonts.googleapis.com/css2?family=Noto+Sans+Mono&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Major+Mono+Display&display=swap);
@import url(https://fonts.googleapis.com/css2?family=VT323&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Press+Start+2P&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Iceland&display=swap);
body{
  background-color: #00501f;
  min-height: 100hv;
}

@-webkit-keyframes glow {
  50% { box-shadow: 5px 15px 50px rgba(145, 245, 167, 0.1);}
}

@keyframes glow {
  50% { box-shadow: 5px 15px 50px rgba(145, 245, 167, 0.1);}
}
@-webkit-keyframes text-glow {
  50% { text-shadow: 0 0 20px rgba(145, 245, 167, 0.1);}
}
@keyframes text-glow {
  50% { text-shadow: 0 0 20px rgba(145, 245, 167, 0.1);}
}

h1{
  font-size: 7rem;
  margin: 1.5vw 1.5vw 0 1.5vw;
  /* margin-bottom: 0; */
}

p{
  margin: 0;
}

a{
  font-size: 1vw;
}

.Button1{
  background-color: tomato;
  border-radius: 25%;
  z-index: 3;
  width: 20px;
  position: absolute;
  right: 1vw;
  top: 1vw;
}

.Button2{
  background-color: rgb(252, 255, 71);
  border-radius: 5px;
  z-index: 3;
  width: 20px;
  position: absolute;
  right: 4vw;
  top: 1vw;
}

.Button3{
  background-color: rgb(71, 255, 132);
  border-radius: 5px;
  z-index: 3;
  width: 20px;
  position: absolute;
  right: 7vw;
  top: 1vw;
}

@media only screen and (max-width: 500px) {
  h1 {
    font-size: 18vw;
  }

  .IntroContainer {
    margin-left: 1em;
    margin-right: 1em;
  }

}

@media only screen and (max-width: 800px) {
  h1 {
    font-size: 15vw;
  }

  .Button1{
    background-color: tomato;
    border-radius: 5px;
    z-index: 3;
    width: 12px;
    height: 12px;
    position: absolute;
    right: 1em;
    top: 1vw;
  }

  .Button2{
    background-color: rgb(252, 255, 71);
    border-radius: 5px;
    z-index: 3;
    width: 12px;
    height: 12px;
    position: absolute;
    right: 2em;
    top: 1vw;
  }

  .Button3{
    background-color: rgb(71, 255, 132);
    border-radius: 5px;
    z-index: 3;
    width: 12px;
    height: 12px;
    position: absolute;
    right: 3em;
    top: 1vw;
  }
}

.MastheadContainer{
    text-align: center;
}

h1{
    color: rgb(145, 245, 167);
    font-family: 'Iceland', cursive;
    text-shadow: 0 0 10px  rgba(145, 245, 167, 0.7);
    -webkit-animation: text-glow 5s infinite;
            animation: text-glow 5s infinite;
}

.SubHeading {
    font-family: 'Iceland', cursive;
    margin-bottom: 2vw;
    margin-top: 0;
    font-size: 200%;
}
h2 {
    color: rgb(145, 245, 167);
    font-family: 'Noto Sans Mono', monospace;
}

p {
    color: rgb(145, 245, 167);
    font-family: 'Noto Sans Mono', monospace;
    text-align: center;
    padding: 2vw;
}

.IntroBottom{
    background-color: black;
    border-color: transparent;
    height: 20%;
    margin-top: 0;
    text-align: center;
    border-radius: 0px 0px 5px 5px;
}

.IntroTop{
    background-color: rgb(74, 2, 207);
    z-index: 2;
    padding:0;
    margin-bottom: 0em;
    height: 8%;
    border-width: 4px;
    border-style: solid;
    border-color: transparent;
    border-radius: 20px 20px 0px 0px;
    position: relative;
}

.ImageDiv {
    width: 80%;
  top: 50%;
  left: 50%;
  z-index: 3;
  text-align: center;
  display: flex;
  flex-direction: row;
  align-content: center;
}

.TitleDiv{
    margin-left: 5%;
    position: relative;
}

.IntroContainer{
    box-shadow: 5px 5px 25px rgba(145, 245, 167, 0.7);
    border-color: transparent;
    -webkit-animation: glow 5s infinite;
            animation: glow 5s infinite;
    margin-left: 3em;
    margin-right: 3em;
    margin-top: 0.1em;
    margin-bottom: 1.5em;
    border-radius: 20px 20px 20px 20px;
    align-content: center;
    align-items: center;
}

@media only screen and (max-width: 800px){
    .IntroContainer {
        flex-direction: column;
        align-items: center;
        margin: 1em;
    }
    
}
a{
    text-decoration: none;
    color: rgb(145, 245, 167);
    font-family: 'Noto Sans Mono', monospace;
    float: right;
    font-size: 150%;
    padding-right: 2em;;
}
a:hover{
    font-weight: bolder;
}
img{
    float: left;
    display: block;
    object-fit: scale-down;
  max-width:100%;
  padding: 1em;
  width: auto;
  height: auto;
}

h2 {
    color: rgb(145, 245, 167);
    font-family: 'Noto Sans Mono', monospace;

}

p {
    color: rgb(145, 245, 167);
    font-family: 'Noto Sans Mono', monospace;
    text-align: center;
    padding: 2vw;
}
.ProjectBottom{
    background-color: black;
    border-color: transparent;
    margin-top: 0;
    text-align: center;
    border-radius: 0px 0px 5px 5px;
    flex: 1 1;
    display: flex;
    justify-content: space-between;
}

.ProjectTop{
    background-color: rgb(74, 2, 207);
    z-index: 2;
    padding:0;
    margin: none;
    height: 8%;
    border-width: 4px;
    border-style: solid;
    border-color: transparent;
    border-radius: 20px 20px 0px 0px;
    position: relative;
    justify-content: center;
}

.TitleDiv{
    margin-left: 5%;
    position: relative;

}

.ProjectContainer{
    box-shadow: 5px 5px 25px rgba(145, 245, 167, 0.7);
    border-color: transparent;
    -webkit-animation: glow 5s infinite;
            animation: glow 5s infinite;
    margin: 3em;
    margin-top: 0.1em;
    border-radius: 20px 20px 20px 20px;
    display: flex;
    flex-direction: column;
    justify-content: center;
}

.TextDiv{
    width: 65%;
    order: 1;
}

@media only screen and (max-width: 800px){
    .TextDiv {
        width: 100%;
        height: -webkit-fit-content;
        height: -moz-fit-content;
        height: fit-content;
    }

    .ProjectContainer {
        flex-direction: column;
        justify-content: center;
        margin: 1em;
        height: -webkit-fit-content;
        height: -moz-fit-content;
        height: fit-content;
        background-color: red;
    }
    
    img {
        display: block;
        padding: 0;
        margin: 0;
        max-width: 100%;
        max-height: 50%;
        
    }

    .ImageDiv {
        width: 90%;
        height: -webkit-fit-content;
        height: -moz-fit-content;
        height: fit-content;
        /* max-height: 50%; */
        object-fit: cover;
        margin-left: auto;
        margin-right: auto;
        text-align: center;
        background-color: aqua;
    }
    
    .ProjectBottom {
        flex-direction: column;
        height: -webkit-fit-content;
        height: -moz-fit-content;
        height: fit-content;
        background-color: green;
    }
}

#ProjectsTitle{
    font-size: 2.5em;
}
.ProjectBoxContainer{
    display: flex;
    flex-direction: column;
}
